import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            list: [],
            user: this.$store.state.userInfo,
            startX: null,
            moveX: null,
            disX: null,
            idx: null,
            page: 1,
            haveMore: true,
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        pull(h){
            this.$post('ConsultAPI/DoctorDuty/GetDoctorDutyList',{
                DoctorId: this.user.Data.User.DoctorId,
                OrgId: this.user.Data.User.OrgId,
                PageIndex: this.page,
                pageSize: 15
            }).then(o => {
                this.pageShow = true
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
                }
                else {
                    this.haveMore = true
                }
                
                if (h){
                    o.ReturnData.forEach(v => {
                        this.list.push(v)
                    })
                }
                else {
                    this.list = o.ReturnData
                }
            })
        },
        modify(i){
            this.$store.dispatch('pushscheduling', i)
            this.$router.push(`/add-scheduling?id=${i.Id}`)
        },
        pop(i){
            if(i.ReserveNums > 0){
                return this.$toast(`当前排班已有人预约，不可删除`)
            }
            this.$post('ConsultAPI/DoctorDuty/DelDoctorDuty',{
                DoctorId: this.user.Data.User.DoctorId,
                Id: i.Id,
            }).then(o => {
                this.pull()
                const el = this.$refs.slider
                el.forEach(v => {
                    v.className = 'scheduling'
                })
            })
        },
        touchStart(ev,idx){
            ev= ev || event
            //tounches类数组，等于1时表示此时有只有一只手指在触摸屏幕
            const el = this.$refs.slider
            el.forEach(v => {
                v.className = 'scheduling'
            })
            if(ev.touches.length == 1){
                // 记录开始位置
                this.startX = ev.touches[0].clientX;
                this.idx = idx
            }
        },
        touchMove(ev){
            ev = ev || event;
                //获取删除按钮的宽度，此宽度为滑块左滑的最大距离
            let wd=this.$refs.remove.offsetWidth;
            if(ev.touches.length == 1) {
                // 滑动时距离浏览器左侧实时距离
                this.moveX = ev.touches[0].clientX
        
                //起始位置减去 实时的滑动的距离，得到手指实时偏移距离
                this.disX = this.startX - this.moveX;
                // 如果是向右滑动或者不滑动，不改变滑块的位置
                if (this.disX > 20) {
                    this.$refs.slider[this.idx].className = 'scheduling offset'
                }
            }
        },
        getMore(){
    		//加载更多
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.getList(true);
                    }
                }, 100)
            }
        },
    }
}



